html,
body {
    font-size: 16px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100vh;
    background-color: rgb(24, 16, 7);
    color: white;
    overflow: hidden;
}

h1 {
    text-align: center
}

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}
